"use client";
import { Brain, Trophy, Target } from 'lucide-react';
import { useRef, useEffect } from 'react';
import { gsap } from 'gsap';

interface GameHeaderProps {
  score: number;
  completedSets: number;
  totalSets: number;
}

export default function GameHeader ({ score, completedSets, totalSets }: GameHeaderProps) {
  const progress = (completedSets / totalSets) * 100;
  const headerRef = useRef<HTMLDivElement>(null);
  const brainRef = useRef<SVGSVGElement>(null);
  const progressBarRef = useRef<HTMLDivElement>(null);
  const scoreRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    if (!headerRef.current || !brainRef.current) return;

    // Animación de entrada del header
    gsap.fromTo(headerRef.current, 
      { 
        y: -100, 
        opacity: 0 
      },
      { 
        y: 0, 
        opacity: 1, 
        duration: 1, 
        ease: "bounce.out" 
      }
    );

    // Animación de pulso neural continuo para el cerebro
    gsap.to(brainRef.current, {
      scale: 1.1,
      duration: 1.5,
      repeat: -1,
      yoyo: true,
      ease: "power2.inOut"
    });
  }, []);

  useEffect(() => {
    // Animación de la barra de progreso
    if (progressBarRef.current) {
      gsap.to(progressBarRef.current, {
        width: `${progress}%`,
        duration: 1,
        ease: "power2.out"
      });
    }
  }, [progress]);

  useEffect(() => {
    // Animación del score cuando cambia
    if (scoreRef.current) {
      gsap.fromTo(scoreRef.current, 
        { 
          scale: 1.3, 
          color: "#f59e0b" 
        },
        { 
          scale: 1, 
          color: "#92400e",
          duration: 0.5, 
          ease: "elastic.out(1, 0.5)" 
        }
      );
    }
  }, [score]);

  return (
    <div ref={headerRef} className="bg-white rounded-3xl p-6 mb-8 shadow-xl border border-gray-200">
      <div className="flex flex-col md:flex-row items-center justify-between gap-6">
        <div className="flex items-center gap-4">
          <div className="bg-gradient-to-br from-blue-500 to-purple-600 p-4 rounded-2xl">
            <Brain ref={brainRef} className="w-8 h-8 text-white" />
          </div>
          <div>
            <h1 className="text-3xl font-bold text-gray-800">
              🧠 Clasifica y Agrupa
            </h1>
            <p className="text-gray-600">
              Conecta los elementos con sus conjuntos neuronales
            </p>
          </div>
        </div>

        <div className="flex items-center gap-6">
          <div className="flex items-center gap-2 bg-yellow-50 px-4 py-2 rounded-xl border border-yellow-200">
            <Trophy className="w-5 h-5 text-yellow-600" />
            <span ref={scoreRef} className="font-bold text-yellow-800">{score} pts</span>
          </div>

          <div className="flex items-center gap-2 bg-blue-50 px-4 py-2 rounded-xl border border-blue-200">
            <Target className="w-5 h-5 text-blue-600" />
            <span className="font-bold text-blue-800">
              {completedSets}/{totalSets} conjuntos
            </span>
          </div>
        </div>
      </div>

      {/* Barra de progreso neuronal */}
      <div className="mt-6">
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm font-semibold text-gray-600">
            Progreso de Conexiones Sinápticas
          </span>
          <span className="text-sm font-bold text-blue-600">
            {Math.round(progress)}%
          </span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-3 overflow-hidden">
          <div 
            ref={progressBarRef}
            className="h-full bg-gradient-to-r from-blue-500 to-green-500 rounded-full"
            style={{ width: '0%' }}
          >
          </div>
        </div>
      </div>
    </div>
  );
};